              </b-tab>
            </b-tabs>
          </div>
          <div class="col-md-3 media">

            <input type="file" class="d-none" id="cr_image"
                   multiple
                   @change="trySubmitFile"
                   accept="image/*"
                   name="datafiles"
                   ref="datafiles"
            >
            <div>
              <label for="cr_image" class="btn btn-primary file_label">planshet_image</label>
            </div>
            <input type="file" class="d-none" id="cr_imagee"
                   multiple
                   @change="trySubmitFile1"
                   accept="image/*"
                   name="datafilesss"
                   ref="datafiless"
            >
            <div>
              <label for="cr_imagee" class="btn btn-primary file_label">Main img</label>
            </div>

            <input type="file" class="d-none" id="cr_imagee"
                   multiple
                   @change="trySubmitFile1"
                   accept="image/*"
                   name="datafilesss"
                   ref="datafiless"
            >
