</template>
<script>
import Navbar from "../../components/Navbar";
export default {
  components: {
    Navbar
  },
  data(){
    return{
      title:'',
      url:'',
      myImages:[],
      imageUrl:[],
      files_name:[],
      myImages1:[],
      imageUrl1:[],
      files_name1:[],
      imageUrl2:[],
      files_name2:[],
      myImages2:[],
      id:'',
      description:'',
      about:'',
      first_task:'',
      second_task:'',
      third_task:'',
      fourth_task:'',
      status:0,
      title_task_1:'',
      title_task_2:'',
      title_task_3:'',
      title_task_4:'',
    }
  },
  methods:{
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    post_ru(){
      const form = new FormData();
      form.append("planshet_image", this.myImages[0]);
      form.append("main_image", this.myImages1[0]);
      form.append("title", this.title);
      form.append("link", this.url);
      form.append("about", this.about);
      form.append("first_task", this.first_task);
      form.append("second_task", this.second_task);
      form.append("third_task", this.third_task);
      form.append("fourth_task", this.fourth_task);

      form.append("first_icon", this.myImages1[2]);
      form.append("second_icon", this.myImages1[3]);
      form.append("third_icon", this.myImages1[4]);
      form.append("fourth_icon", this.myImages1[5]);

      form.append("first_title_task", this.title_task_1);
      form.append("second_title_task", this.title_task_2);
      form.append("third_title_task", this.title_task_3);
      form.append("fourth_title_task", this.title_task_4);
      form.append("status", this.status);
      this.$http.post('/api/direction/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'ru'
            }
          })

          .then(res=> {
            this.$toasted.show("Create data success", {
              duration: 1000,
              'type': 'info'

            })
            this.id=res.data.id
          })
    },
    put_uzl(){
      const form = new FormData();
      form.append("planshet_image", this.myImages[0]);
      form.append("main_image", this.myImages1[0]);
      form.append("title_task", this.title_task);
      form.append("title", this.title);
      form.append("link", this.url);
      form.append("about", this.about);
      form.append("first_title_task", this.title_task_1);
      form.append("second_title_task", this.title_task_2);
      form.append("third_title_task", this.title_task_3);
      form.append("fourth_title_task", this.title_task_4);
      form.append("first_task", this.first_task);
      form.append("second_task", this.second_task);
      form.append("third_task", this.third_task);
      form.append("fourth_task", this.fourth_task);
      form.append("status", this.status);

      this.$http.put('/api/direction/'+this.$route.query.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-latn'
            }
          })

          .then(()=> {
            this.$toasted.show("Create data success", {
              duration: 1000,
              'type': 'info'

            })
          })
    },
    put_uzk(){

      const form = new FormData();
      form.append("pc_image", this.myImages[0]);
      form.append("title", this.title);
      form.append("link", this.url);
      form.append("about", this.about);
      form.append("first_task", this.first_task);
      form.append("second_task", this.second_task);
      form.append("third_task", this.third_task);
      form.append("fourth_task", this.fourth_task);
      form.append("status", this.status);
      form.append("description", this.description);
      this.$http.put('/api/direction/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-cyrl'
            }
          })

          .then(()=> {
            this.$toasted.show("Create data success", {
              duration: 1000,
              'type': 'info'

            })
          })
    },
    put_en(){

      const form = new FormData();
      form.append("pc_image", this.myImages[0]);
      form.append("title", this.title);
      form.append("link", this.url);
      form.append("about", this.about);
      form.append("first_task", this.first_task);
      form.append("second_task", this.second_task);
      form.append("third_task", this.third_task);
      form.append("fourth_task", this.fourth_task);
      form.append("status", this.status);
      form.append("description", this.description);
      this.$http.put('/api/direction/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'en'
            }
          })

          .then(()=> {
            this.$toasted.show("Create data success", {
              duration: 1000,
              'type': 'info'

            })
          })
    }
  }
}
</script>
